import React from 'react'
import { useLocation , Route,Routes} from 'react-router-dom'
import { AnimatePresence } from 'framer-motion';
import Home from './Home';
import Aboutus from './Aboutus';
import OurTeam from './OurTeam';
import Projects from './Projects';
function AnimateRoutes() {
  const location = useLocation();

  return (
    <div>
        <AnimatePresence key={location.pathname}>
        <Routes>
          <Route path='/' element={<Home/>}/>
          <Route path='/aboutus' element={<Aboutus/>}/>
          <Route path='/projects' element={<Projects/>}/>
          <Route path='/projects/:javafullstack' element={<Projects/>}/>
          <Route path='/projects/:mernstack' element={<Projects/>}/>
          <Route path='/projects/:data-analysis' element={<Projects/>}/>
          <Route path='/projects/:meanstack' element={<Projects/>}/>
          <Route path='/projects/:machinelearning' element={<Projects/>}/>
          <Route path='/projects/:pythonstack' element={<Projects/>}/>
          <Route path='/ourteam' element={<OurTeam/>}/>
        </Routes>
        </AnimatePresence>
    </div>
  )
}

export default AnimateRoutes